<template>
	<div id="app">
		<router-view></router-view>
	</div>
</template>

<script>
	export default {
		name: 'app',
		data() {
			return {
				msg: 'Welcome to Your Vue.js App'
			}
		}
	}
</script>

<style>
	html {
		font-size: 42px;
	}

	body {
		min-width: 320px;
		max-width: 750px;
	}
	
	body {
		background-color: #f9f9f9;
	}

	#app {
		margin: 0 auto;
	}

	a {
		text-decoration: none;
	}

	input {
		border: none;
		outline: none;
	}

	.clearfix:after {
		content: '';
		display: block;
		clear: both;
	}
	
	.header {
		display: flex;
		height: 1rem;
		background-color: #333;
		font-weight: bold;
		color: #fff;
	}
	
	.header .back,
	.header .homepage {
		display: block;
		width: 0.92rem;
		height: 0.48rem;
		position: relative;
		top: 0.26rem;
	}
	
	.header .back {
		background: url(../images/icon_fanhui.png) no-repeat;
		background-size: 0.48rem;
		left: 0.2rem;
	}
	
	.header .homepage {
		background: url(../images/icon_homepage.png) no-repeat right top;
		background-size: 0.48rem;
		right: 0.2rem;
	}
	
	.header .title {
		width: 100%;
		height: 100%;
		line-height: 1rem;
		text-align: center;
		font-size: 0.4rem;
		color: #fff;
	}

	.footer {
		width: 100%;
		background-color: #444;
	}

	.footer .link {
		padding: 0.4rem 0 0.38rem;
		line-height: 0.28rem;
		font-size: 0.28rem;
		text-align: center;
		color: #fff;
	}

	.footer .link a {
		color: #fff;
		padding: 0 0.2rem;
	}

	.footer p {
		line-height: 0.24rem;
		padding-bottom: 0.38rem;
		text-align: center;
		font-size: 0.22rem;
		color: #fff;
	}
	
	.footer p span {
		padding: 0 0.16rem;
	}
</style>



