import VueRouter from 'vue-router';

import start from '../component/start.vue';
import search from '../component/search.vue';

import login from '../component/login/login.vue';
import sign from '../component/login/sign.vue';

import nav from '../component/nav/nav.vue';
import news from '../component/news/news.vue';
import tv from '../component/tv/tv.vue';


export default new VueRouter({
	routes: [{
			path: '/',
			component: start
		}, {
			path: '/login',
			component: login
		}, {
			path: '/sign',
			component: sign
		}, {
			path: '/search',
			component: search
		}, {
			path: '/nav',
			component: nav
		}, {
			path: '/news/:id',
			component: news
		}, {
			path: '/tv/:id',
			component: tv
		}]
})



