<template>
	<div>
		<header class="header">
			<div class="back" @click="back()"></div>
			<p class="title">全站导航</p>
			<router-link to="/tv/0" class="homepage"></router-link>
		</header>
		<div class="top">
			<ul v-if="data.top">
				<li v-for="(v,i) in data.top">
					<router-link :to="v.path">
						<div class="pic-img ">
							<img :src="v.src">
						</div>
						<p class="pic-title">{{v.title}}</p>
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				data: {}
			}
		},
		mounted() {
			let _this = this;
			this.$http.get('./data/nav.json')
				.then((res) => {
					_this.data = res.data;
				}).catch((err) => {
					console.log(err);
				})
		},
		methods: {
			back() {
				this.$router.go(-1);
			}
		}
	}
</script>

<style scoped>
	.top {
		padding: 0.36rem 2.66%;
		border-top: 0.18rem solid #f4f4f4;
		background-color: #fff;
	}

	.top ul {
		display: flex;
		flex-flow: row nowrap;
		justify-content: center;
		align-items: center;
	}

	.top li {
		width: 25%;
		
	}

	.top li a {
		display: block;
	}

	.top .pic-img {
		width: 1.1rem;
		height: 1.1rem;
		margin: 0 auto 0.18rem;
	}

	.top .pic-img img {
		width: 100%;
		height: 100%;
	}

	.top .pic-title {
		width: 100%;
		text-align: center;
		font-size: 0.3rem;
		color: #333;
	}
</style>



