<template>
	<div>
		<header>
			<router-link to="/tv/0" class="home"></router-link>
			<div class="hsearch-box">
				<input type="text" placeholder="诗词大会">
				<span class="search"></span>
			</div>
			<router-link to="/nav" class="menu"></router-link>
		</header>
		<nav v-if="data.nav">
			<div class="swiper-container nav-list">
				<ul class="swiper-wrapper">
					<li v-for="(v,i) in data.nav" :class="{'swiper-slide':true,'cur':(i==id)}" @click="changeNav(i)">
						<div class="item">{{v}}
							<span class="hot" v-if="i==1">HOT</span>
						</div>
					</li>
				</ul>
			</div>
		</nav>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				data: {},
				id: 0
			}
		},
		mounted() {
			this.id = this.$route.params.id;
			let _this = this;
			this.$http.get('./data/tv.json')
				.then((res) => {
					_this.data = res.data;
					_this.$nextTick(() => {
						var swiperNav = new Swiper('nav .swiper-container', {
							slidesPerView: 'auto',
							freeModeSticky: true
						})
					})
				}).catch((err) => {
					console.log(err)
				})
		},
		methods: {
			changeNav(i) {
				this.id = i;
			}
		}
	}
</script>

<style scoped>
	header {
		display: flex;
		flex-flow: row nowrap;
		height: 1.08rem;
		padding-top: 0.26rem;
		background-color: #333;
		box-sizing: border-box;
	}

	header .home {
		display: block;
		width: 0.94rem;
		height: 0.5rem;
		padding-right: 0.2rem;
		margin-left: 0.2rem;
		border-right: 1px solid #666;
		background: url(../../../images/tv/logo.png) no-repeat top left;
		background-size: 0.94rem 0.5rem;
	}

	header .hsearch-box {
		flex: 1;
		height: 0.48rem;
		margin: 0 0.48rem;
		position: relative;
		box-sizing: border-box;
	}

	header .hsearch-box input {
		width: 100%;
		height: 0.48rem;
		padding: 0.09rem 0.2rem 0.09rem 0.6rem;
		vertical-align: middle;
		border: 1px solid #fff;
		border-radius: 1.2rem;
		background: #333;
		box-sizing: border-box;
		font-size: 0.3rem;
		color: #d3d3d3;
		font-style: normal;
		outline: none;
		position: absolute;
		top: 0;
		left: 0;
	}

	header .hsearch-box .search {
		width: 0.36rem;
		height: 0.36rem;
		position: absolute;
		top: 0.06rem;
		left: 0.2rem;
		background: url(../../../images/tv/searbor.png) no-repeat center;
		background-size: 100%;
	}

	header .menu {
		display: block;
		width: 0.48rem;
		height: 0.48rem;
		padding: 0 0.2rem;
		border-left: 1px solid #666;
		background: url(../../../images/tv/menu.png) no-repeat top center;
		background-size: 0.48rem 0.48rem;
	}

	nav {
		height: 1rem;
		padding: 0 0.2rem;
		overflow: hidden;
		background-color: #f4f4f4;
	}

	nav .nav-list {
		height: 1rem;
	}

	nav li {
		display: inline-block;
		width: auto;
		line-height: 0.36rem;
		padding: 0.32rem 0.3rem;
		box-sizing: border-box;
		font-size: 0.36rem;
		color: #333;
	}

	nav li:first-child {
		padding-left: 0;
	}

	nav li.cur {
		font-weight: bold;
		color: #e42626;
		position: relative;
	}

	nav li:first-child.cur:after {
		margin-left: -0.305rem;
	}

	nav li.cur:after {
		content: '';
		width: 0.4rem;
		height: 0.02rem;
		background-color: #e42626;
		position: absolute;
		left: 50%;
		bottom: 0.15rem;
		margin-left: -0.2rem;
	}

	nav li .item {
		display: inline-block;
		position: relative;
	}

	nav li .item .hot {
		display: block;
		height: 0.36rem;
		line-height: 0.36rem;
		padding: 0 .06rem;
		border-radius: 0.28rem;
		background-color: red;
		background-clip: padding-box;
		font-size: 0.22rem;
		font-weight: normal;
		text-align: center;
		color: #fff;
		overflow: hidden;
		position: absolute;
		top: -0.24rem;
		right: -0.48rem;
	}
</style>



