<template>
	<div>
		<header class="clearfix">
			<a href="http://m.cctv.com" target="_blank" class="icon-logo"></a>
			<router-link to="/search" class="icon-search"></router-link>
		</header>
		<nav v-if="data.nav">
			<div class="swiper-container nav-list">
				<div class="swiper-wrapper">
					<div class="swiper-slide list-item" v-for="(v,i) in data.nav.slide">
						<router-link :to="`/news/${v.id}`" :class="{'active':(i==0)}">{{v.txt}}</router-link>
					</div>
				</div>
			</div>
			<div class="icon-menu" @click="showfuceng(2)"></div>
		</nav>
		<div class="toutiao" v-if="data.toutiao">
			<router-link :to="`/news/${data.toutiao.id}`" class="main clearfix">
				<div class="left">头<br>条</div>
				<div class="right">{{data.toutiao.title}}</div>
			</router-link>
		</div>
		<div class="lunbo" v-if="data.lunbo">
			<div class="swiper-container lunbo-list">
				<div class="swiper-wrapper">
					<div class="swiper-slide list-item" v-for="(v,i) in data.lunbo">
						<router-link to="/news">
							<img :src="v.src">
							<div class="text">
								<p>{{v.txt}}</p>
							</div>
							<div class="swiper-pagination"></div>
						</router-link>
					</div>
				</div>
			</div>
		</div>
		<div class="tabbar">
			<ul class="clearfix" v-if="data.tabbar">
				<li v-for="(v,i) in data.tabbar" :style="v.bgImg">
					<router-link :to="v.path" v-if="i!=2">{{v.text}}</router-link>
					<div v-if="i==2" :class="kandian" @click="showfuceng(0)"></div>
				</li>
			</ul>
		</div>
		<div class="fuceng" :style="fcstyle">
			<div class="clearfix">
				<div class="icon_guanbi" @click="showfuceng(1)"></div>
			</div>
			<div class="kandianbox">
				<ul v-if="data.kdlist">
					<li v-for="(v,i) in data.kdlist.content">
						<router-link :to="`/tv/${i+1}`">
							<b>{{data.kdlist.title[i]}}</b>{{v}}
						</router-link>
					</li>
					<li v-for="(v,i) in data.kdlist.excon" :class="v.css">
						<router-link :to="`/tv/${i+17}`" style="padding: 0;width: 97%;">
							<img :src="v.src" v-if="v.src!=''">
							<span v-if="v.con!=''">{{v.con}}</span>
						</router-link>
					</li>
				</ul>
			</div>
		</div>
		<div class="menufuceng" :style="menufcstyle">
			<div class="md-box">
				<div class="md-hd clearfix">
					<span class="mh-title">全站地图</span>
					<span class="mh-guanbi" @click="showfuceng(3)"></span>
				</div>
				<guide></guide>
			</div>
		</div>
	</div>
</template>
<script>
	import Swiper from 'swiper';
	import guide from './guide.vue';
	export default {
		data() {
			return {
				data: {},
				kandian: 'kandian',
				fcstyle: {
					top: '120%'
				},
				menufcstyle: {
					top: '120%'
				}
			}
		},
		mounted() {
			let _this = this;
			this.$http.get('./data/start.json')
				.then((res) => {
					_this.data = res.data;
					_this.$nextTick(() => {
						var swiperNav = new Swiper('nav .swiper-container', {
							slidesPerView: 5,
							freeModeSticky: true
						})
						var swiperLunbo = new Swiper('.lunbo .swiper-container', {
							effect: 'coverflow',
							slidesPerView: 1.2,
							loop: true,
							centeredSlides: true,
							autoplay: 3000,
							coverflow: {
								rotate: 0,
								stretch: 0,
								depth: 100,
								modifier: 1.5,
								slideShadows: false
							},
							pagination: '.lunbo .swiper-pagination',
							paginationType: 'fraction',
							paginationFractionRender: (swiper, currentClass, totalClass)=>{
								return `<span class="${currentClass}" style="font-size: 0.4rem;color: #bf0614;"></span> / <span class="${totalClass}"></span>`;
							}
						})
					})
				}).catch((err) => {
					console.log(err)
				})
		},
		methods: {
			showfuceng(flag) {
				if (flag == 0) {
					this.kandian = 'kandian cur';
					this.fcstyle.top = '0%';
				} else if (flag == 1) {
					this.kandian = 'kandian';
					this.fcstyle.top = '120%';
				} else if (flag == 2) {
					this.menufcstyle.top = '0%';
				} else if (flag == 3) {
					this.menufcstyle.top = '120%';
				}
			}
		},
		components: {
			'guide': guide
		}
	}
</script>
<style scoped src="../css/start.css"></style>



