<template>
	<div class="md-bd" v-if="data.guide">
		<ul class="quanzhan clearfix">
			<li v-for="(v,i) in data.guide.channel" :class="{last:(i && (i+1)%4==0)}">
				<router-link :to="`/news/${v.id}`">{{v.txt}}</router-link>
			</li>
		</ul>
		<div style="height: 0.4rem"></div>
		<ul class="quanzhan clearfix">
			<li v-for="(v,i) in data.guide.hot" :class="{tab2:true,last:(i && (i+1)%2==0)}">
				<router-link to="/news">{{v}}</router-link>
			</li>
			<li v-for="(v,i) in data.guide.exhot" :class="{last:(i && (i+1)%4==0)}">
				<router-link to="/news">{{v}}</router-link>
			</li>
		</ul>
		<div style="height: 0.4rem"></div>
		<ul class="quanzhan clearfix">
			<li v-for="(v,i) in data.guide.docu" :class="{tab2:true,last:(i && (i+1)%2==0)}">
				<router-link to="/news">{{v}}</router-link>
			</li>
		</ul>
		<div class="title">
			<p class="con">CCTV.节目官网</p>
		</div>
		<ul class="quanzhan clearfix">
			<li v-for="(v,i) in data.guide.website" :class="{last:(i && (i+1)%4==0)}">
				<router-link to="/news">{{v}}</router-link>
			</li>
		</ul>
		<ul class="quanzhanlogo clearfix">
			<li v-for="(v,i) in data.guide.logotitle" :class="{last:(i && (i+1)%4==0)}">
				<router-link :to="`/tv/${i+1}`">
					<img :src="`./images/${v}.jpg`">
				</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				data: {}
			}
		},
		mounted() {
			let _this = this;
			this.$http.get('./data/guide.json')
				.then((res) => {
					_this.data = res.data;
				}).catch((err) => {
					console.log(err)
				})
		}
	}
</script>

<style scoped>
	.md-bd .quanzhan {
		border-bottom: 1px solid #e7e7e7;
	}

	.md-bd .quanzhan li {
		float: left;
		width: 21.52%;
		margin-right: 4.64%;
		margin-bottom: 0.4rem;
		color: #333333;
		font-size: 0.32rem;
		line-height: 0.6rem;
		text-align: center;
	}

	.md-bd .quanzhan li.tab2 {
		width: 47.68%;
	}

	.md-bd .quanzhan li.last {
		margin-right: 0;
	}

	.md-bd .quanzhan a {
		display: block;
		width: 100%;
		border: 1px solid #999999;
		box-sizing: border-box;
		color: #333333;
		border-radius: 0.12rem;
	}

	.md-bd .quanzhanlogo {
		padding-top: 0.1rem;
		padding-bottom: 0.3rem;
		display: flex;
		flex-flow: row wrap;
	}

	.md-bd .quanzhanlogo li {
		width: 21.52%;
		margin: 0.3rem 0;
		margin-right: 4.64%;
	}

	.md-bd .quanzhanlogo li.last {
		margin-right: 0;
	}

	.md-bd .quanzhanlogo li img {
		display: block;
		width: 100%;
	}

	.md-bd .title {
		font-weight: bold;
		color: #bf0614;
		font-size: 0.32rem;
	}

	.md-bd .title .con {
		display: inline-block;
		height: 0.5rem;
		line-height: 0.5rem;
		padding: 0.3rem 0.4rem 0.3rem 0;
		background: url(../../images/btnjiantou.png) no-repeat right center;
		background-size: 0.2rem 0.2rem;
	}
</style>


