<template>
	<div>
		<header class="header">
			<div class="back" @click="back()"></div>
			<p class="title">用户登陆</p>
		</header>
		<div class="third-login">
			<div class="top">其他帐号一键登录</div>
			<ul class="item-box" v-if="data.icon">
				<li class="item" v-for="(v,i) in data.icon">
					<img :src="v.src">
					<p>{{v.txt}}</p>
				</li>
			</ul>
		</div>
		<div class="user-login"></div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				data: {}
			}
		},
		mounted() {
			let _this = this;
			this.$http.get('./data/login.json')
				.then((res) => {
					_this.data = res.data;
				}).catch((err) => {
					console.log(err);
				})
		},
		methods: {
			back() {
				this.$router.go(-1);
			}
		}
	}
</script>

<style scoped>
	.header {
		height: 1.19rem;
		background-color: #114590;
		position: relative;
	}

	.header .back {
		width: 1.19rem;
		height: 1.19rem;
		position: absolute;
		top: 0;
		left: 0;
		background-position: 0.29rem 0.29rem;
		background-size: 0.59rem auto;
		z-index: 999;
	}

	.header .title {
		line-height: 1.19rem;
		font-size: 0.43rem;
		text-align: center;
		position: absolute;
	}

	.third-login {
		width: 90%;
		height: 2.3rem;
		margin: 0.19rem auto 0;
		background: url(../../../images/tv/kuang.png) no-repeat;
		background-size: 100% 100%;
	}

	.third-login .top {
		font-size: 0.33rem;
		font-family: "微软雅黑";
		text-align: center;
		color: #b2b2b2;
	}
	
	.third-login .item-box {
		display: flex;
		flex-flow: row nowrap;
		justify-content: center;
		align-items: center;
		margin-top: 0.238rem;
	}
	
	.third-login .item {
		width: 33%;
	}
	
	.third-login .item img {
		display: block;
		width: 1.02rem;
		height: 1.02rem;
		margin: 0 auto;
	}
	
	.third-login .item p {
		font-size: 0.28rem;
		font-family: "微软雅黑";
		text-align: center;
		color: #b2b2b2;
	}

	.user-login {
		height: 219px;
	}
</style>



