<template>
	<div>
		<header class="header">
			<div class="back" @click="back()"></div>
			<p class="title">搜索</p>
			<router-link to="/" class="homepage"></router-link>
		</header>
		<div class="top-search">
			<div class="search-area">
				<input type="text" placeholder="请输入您想搜索的关键词" id="search">
				<div class="search-btn"><i></i></div>
			</div>
		</div>
		<div style="height: 0.24rem;background-color: #E3E8F2;"></div>
		<div class="today-search">
			<h3>今日搜索</h3>
			<ul class="defualt-list">
				<li v-for="(v,i) in data.today" :class="{'differ':(i+3)%4==0 || (i+2)%4==0}">
					<router-link to="/news">{{v}}</router-link>
				</li>
			</ul>
		</div>
		<div style="height: 2.85rem;"></div>
		<div class="footer">
			<div class="link">
				<router-link to="/">首页</router-link>|
				<router-link to="/">全站地图</router-link>
			</div>
			<p>
				<span>中央广播电视总台</span>
				<span>央视网</span>
				<span>版权所有</span>
			</p>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				data: {}
			}
		},
		mounted() {
			let _this = this;
			this.$http.get('./data/search.json')
				.then((res) => {
					_this.data = res.data;
					_this.$nextTick(()=>{
						document.getElementById("search").focus();
					})
				}).catch((err) => {
					console.log(err);
				})
		},
		methods: {
			back() {
				this.$router.go(-1);
			}
		}
	}
</script>

<style scoped>
	.header {
		background-color: #bf0614;
	}
	
	.top-search {
		padding: 0.48rem 0.2rem 0.33rem;
		position: relative;
		z-index: 100;
	}

	.search-area {
		display: flex;
		align-items: center;
		width: 100%;
		position: relative;
		overflow: hidden;
	}

	.search-area input {
		display: block;
		width: 90%;
		height: 0.72rem;
		padding: 0.18rem 0.68rem 0.18rem 0.12rem;
		border: 1px solid #bf0614;
		font-size: 0.36rem;
		color: #333;
		box-sizing: border-box;
	}

	.search-area .search-btn {
		width: 10%;
		height: 0.72rem;
		background-color: #bf0614;
	}

	.search-area .search-btn i {
		display: block;
		width: 100%;
		height: 100%;
		background: url(../../images/search.png) no-repeat center center;
		background-size: 0.48rem 0.48rem;
	}

	.today-search {
		padding: 0 0.2rem;
	}

	.today-search h3 {
		line-height: 0.4rem;
		padding: 0.25rem 0;
		font-size: 0.4rem;
		font-weight: bold;
		color: #bf0614;
		border-bottom: 1px solid #E4E4E4;
	}

	.today-search .defualt-list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-flow: row wrap;
		padding-top: 0.36rem;
	}

	.today-search .defualt-list li {
		width: 49.5%;
		height: 0.72rem;
		line-height: 0.72rem;
		margin-right: 1%;
		margin-bottom: 1%;
		background-color: #f7f7f7;
		text-align: center;
		font-size: 0.32rem;
		font-weight: bold;
	}
	
	.today-search .defualt-list li.differ {
		background-color: #f2f2f2;
	}
	
	.today-search .defualt-list li:nth-of-type(2n){
		margin-right: 0;
	}

	.today-search .defualt-list a {
		display: block;
		width: 92%;
		margin: 0 auto;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 0.36rem;
		font-weight: normal;
		color: #333333;
	}
</style>



